














/* 解决vue页面闪烁问题 */
[v-cloak] {
    display: none!important;
}
/* 全局视图容器 */
.g-container_view{
    /* width: 1200px;
    margin: 0 auto; */
    position: relative;
}
/* 公共样式 */
*{
    margin: 0;
    padding: 0;
    -webkit-box-sizing: content-box;
            box-sizing: content-box;
}
li{
    list-style: none;
}
h1,h2,h3,h4,h5,h6{
    font-weight: normal;
}
i,em{
    display: inline-block;
    font-style: normal;
}
a{
    text-decoration: none;
    -webkit-tap-highlight-color: transparent;
}
a:hover{
    cursor: pointer;
}
button,a,span,input,img,textarea{
    display: inline-block;
    vertical-align: bottom;
    border: none;
    outline: none;
}
input,textarea{
    background-color: transparent;
}
img{
    width: 100%;
    height: 100%;
}
body{
    font-size: 12px;
    color: #333;
    font-family: "\5FAE\8F6F\96C5\9ED1","\5B8B\4F53","\9ED1\4F53";
}
/* 清除浮动 */
.clearfix{
/* 触发hasLayout */
    *zoom: 1;
}
.clearfix:after{
    content: ".";
    display: block;
    clear: both;
    overflow: hidden;
    height: 0;
    visibility: hidden;
}
























































































































































































































































